import { useState } from 'react';
import { Table } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import styled from 'styled-components';
import { changeName, MinusCount, PlusCount } from "./../store.js";

function Cart() {

    let cart = useSelector((state) => state.cart)
    let dispatch = useDispatch();

    console.log({cart})

    return (
        <div>
            <Table>
                <thead>
                    <tr>
                        <th>상품</th>
                        <th>상품명</th>
                        <th>수량</th>
                        <th>변경하기</th>
                    </tr>
                </thead>
                <tbody>
                {cart.map((a, i) =>
                    <tr>
                        <td><img src={'https://codingapple1.github.io/shop/shoes'+ (cart[i].id+1) +'.jpg'} style={{width:'70px'}}/></td>
                        <td>{cart[i].name}</td>
                        <td>{cart[i].count}</td>
                        <td>
                            <button onClick={() => {dispatch(MinusCount(cart[i].id))}} style={{border:'none', borderRadius:'50%', backgroundColor:'yellow'}}>-</button>
                            <button onClick={() => {dispatch(PlusCount(cart[i].id))}} style={{border:'none', borderRadius:'50%', backgroundColor:'yellow'}}>+</button>
                        </td>
                    </tr>        
                )}
                </tbody>
            </Table>
        </div>
    )
}

export default Cart;